import { IonItem, IonList, IonText } from "@ionic/react";
import "./ExploreContainer.css";
import Axios from "axios";
import { useEffect, useState } from "react";
import ReactPlayer from "react-player/youtube";
import { LocalNotifications } from "@capacitor/local-notifications";



const Home: React.FC = () => {
  const [videoData, setVideoData] = useState([]);

  LocalNotifications.schedule({
    notifications: [
      {
        title: "Live Katha",
        body: "katha will start at 1:30",
        id: 1,
        schedule: { at: new Date(new Date().getTime() + 3600) },
        sound: "./sound/sound.mp3",
      },
    ],
  });

  useEffect(() => {
      Axios.get("https://lazy-tan-penguin-hose.cyclic.app/videos").then((res: any) => {
        console.log(res.data);
        setVideoData(res.data);
      }).catch(e=>{
        
      })
  }, []);

  return (
    <>
      <div>
        <IonList>
          {videoData.slice(0).reverse().map((video: any, index) => {
            const { url, title, description, tags, id } = video;
            return (
              <>
                <div  key={index}>
                  <IonItem key={id}>          
                      <ReactPlayer
                      key={id}
                        controls
                        url={url}
                        // playing
                      />
                  </IonItem>
                  
                  <IonItem key={id}>
                      <IonText key={id}>
                      <h2>{title}</h2>
                        <p>{description}</p>
                        <h6>
                          <b>{tags}</b>
                        </h6>
                      </IonText>
                      
                  </IonItem>
                </div>
              </>
            );
          })}
        </IonList>
      </div>
    </>
  );
};

export default Home;
